@media only screen and (min-aspect-ratio: 1/1) {
    .backMenuContainer{
        rotate: 180deg;
        top:0;
        left:0;
        position:absolute;
        display:flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        font-size: 2vh;
        gap:5%;
        width: 100%;
        height: 100%;
        background-color: black;
    }
    }
    @media only screen and (max-aspect-ratio: 1/1) {
        .backMenuContainer{
            rotate: 180deg;
            top:0;
            left:0;
            position:absolute;
            display:flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            font-size: 2vw;
            gap:5%;
            width: 100%;
            height: 100%;
            background-color: black;
        }}

