.backMenuContainer__BlueHover{
    background-color: blue;
    box-shadow: 0px 0px 5vh blue;
    color:white;
    text-shadow: 0px 0px 1vh white;
    transition: 0.2s ease-in-out;
}.backMenuContainer__BlueNotHover{
    background-color:rgb(33, 33, 33);;
    color:black;
    transition: 0.4s ease-in-out;
}.backMenuContainer__BlueGameMode{
    background-color: blue;
    box-shadow: 0px 0px 5vh blue;
    color:white;
    text-shadow: 0px 0px 1vh white;
}

.whiteLoad_BackMenu{
